<template>
  <div>
    <v-container>
      <PageTitle title="6 Tryout Diikuti" />
      <v-row>
        <v-col cols="12">
          <v-text-field outlined placeholder="Cari" class="white" hide-details="true"></v-text-field>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12">
          <v-card
            class="white"
            tile
            v-for="(item, index) in boughtTryoutList"
            :key="index"
            link
            :to="{ name: 'UserTryoutDetail'}"
          >
            <v-row class="pa-3">
              <v-col xs="12" md="6">
                <div :class="listCaptionStyle">Nama Tryout</div>
                <div>{{ item.name }}</div>
              </v-col>
              <v-col xs="6" sm="4" md="2">
                <div :class="listCaptionStyle">Kategori</div>
                <div>{{ item.category }}</div>
              </v-col>
              <v-col xs="6" sm="4" md="2">
                <div :class="listCaptionStyle">Jadwal</div>
                <div>{{ item.scheduledAt }}</div>
              </v-col>
              <v-col xs="2" sm="4" md="2">
                <div align="end">
                  <v-chip
                    small
                    :class="getStatusChipClass(item.status)"
                  >{{getStatusText(item.status)}}</v-chip>
                </div>
              </v-col>
            </v-row>
            <v-divider></v-divider>
          </v-card>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12" class="text-center">
          <v-btn color="primary" text small>Lihat selengkapnya</v-btn>
        </v-col>
      </v-row>
      <v-divider></v-divider>
      <PageTitle title="3 Konsultasi Diikuti" />
      <v-row>
        <v-col cols="12">
          <v-text-field outlined placeholder="Cari" class="white" hide-details="true"></v-text-field>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12">
          <v-card
            class="white"
            tile
            v-for="(item, index) in boughtTryoutList"
            :key="index"
            link
            :to="{ name: 'UserTryoutDetail'}"
          >
            <v-row class="pa-3">
              <v-col xs="12" md="6">
                <div :class="listCaptionStyle">Nama Tryout</div>
                <div>{{ item.name }}</div>
              </v-col>
              <v-col xs="6" sm="4" md="2">
                <div :class="listCaptionStyle">Kategori</div>
                <div>{{ item.category }}</div>
              </v-col>
              <v-col xs="6" sm="4" md="2">
                <div :class="listCaptionStyle">Jadwal</div>
                <div>{{ item.scheduledAt }}</div>
              </v-col>
              <v-col xs="2" sm="4" md="2">
                <div align="end">
                  <v-chip
                    small
                    :class="getStatusChipClass(item.status)"
                  >{{getStatusText(item.status)}}</v-chip>
                </div>
              </v-col>
            </v-row>
            <v-divider></v-divider>
          </v-card>
        </v-col>
        <v-row>
          <v-col cols="12" class="text-center">
            <v-btn color="primary" text small>Lihat selengkapnya</v-btn>
          </v-col>
        </v-row>
      </v-row>
    </v-container>
  </div>
</template>
<script>
import BoughtTryoutModel from "@/models/boughtTryoutModel";
import componentMixin from "@/mixins/componentMixin";
import PageTitle from "./PageTitle.vue";

export default {
  name: "UserDashboard",
  components: { PageTitle },
  data: () => ({
    pageName: "Dasbor",
    listCaptionStyle: "caption grey--text",
    boughtTryoutList: [
      new BoughtTryoutModel(
        "Tryout 1",
        "Saintek",
        "17 Agustus 2020 pukul 08.00 - 12.00",
        "todo"
      ),
      new BoughtTryoutModel(
        "Tryout 2",
        "Soshum",
        "18 Agustus 2020 pukul 08.00 - 12.00",
        "ongoing"
      ),
      new BoughtTryoutModel(
        "Tryout 3",
        "Saintek",
        "19 Agustus 2020 pukul 08.00 - 12.00",
        "done"
      ),
    ],
  }),
  methods: {
    getStatusText(status) {
      if (status === "todo") {
        return "BELUM DIKERJAKAN";
      } else if (status === "ongoing") {
        return "SEDANG DIKERJAKAN";
      } else {
        return "SELESAI DIKERJAKAN";
      }
    },
    getStatusChipClass(status) {
      if (status === "todo") {
        return "error";
      } else if (status === "ongoing") {
        return "warning";
      } else {
        return "success";
      }
    },
  },
  created() {
    this.notifyPageChanged(this.pageName);
  },
  mixins: [componentMixin],
};
</script>